    opacity: 0;
    transition: 0.5s;
    transition-duration: 1.5s;
    transform: translateY(100px);
  }
}

@media print, screen and (min-width: 768px) {
  .--typeScrollFadeIn.active {
    opacity: 1;
    transform: translateY(0);
  }
}

/*ラインから出現*/
@media print, screen and (min-width: 768px) {
  .--typeScrollLine {
    --delay: 0;
    --duration: 800ms;
    --iterations: 1;
    opacity: 0;
  }
}

@media print, screen and (min-width: 768px) {
  .--typeScrollLine.active,
.--typeScrollLine.active::after {
    -webkit-animation-delay: var(--animation-delay, 2s);
    animation-delay: var(--animation-delay, 2s);
    -webkit-animation-iteration-count: var(--iterations, 1);
    animation-iteration-count: var(--iterations, 1);
    -webkit-animation-duration: var(--duration, 800ms);
    animation-duration: var(--duration, 800ms);
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}

@media print, screen and (min-width: 768px) {
  .--typeScrollLine.active {
    opacity: 1;
    --animation-delay: var(--delay, 0);
    --animation-duration: var(--duration, 800ms);
    --animation-iterations: var(--iterations, 1);
    position: relative;
    -webkit-animation-name: clip-text;
    animation-name: clip-text;
    white-space: nowrap;
    cursor: default;
  }
}
@media print, screen and (min-width: 768px) {
  .--typeScrollLine.active::after {
    content: "";
    position: absolute;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000;
    transform: scaleX(0);
    transform-origin: 0 50%;
    pointer-events: none;
    -webkit-animation-name: text-revealer;
    animation-name: text-revealer;
  }
}

.mainVisual .--typeScrollLine.active::after {
  background-color: #FFF;
}

.mainVisual .--typeScrollLine.--type01.active,
.mainVisual .--typeScrollLine.--type01.active::after {
  animation-delay: 800ms;
}

.mainVisual .--typeScrollLine.--type02.active,
.mainVisual .--typeScrollLine.--type02.active::after {
  animation-delay: 1300ms;
}

@-webkit-keyframes clip-text {
  from {
    -webkit-clip-path: inset(0 100% 0 0);
    clip-path: inset(0 100% 0 0);
  }
  to {
    -webkit-clip-path: inset(0 0 0 0);
    clip-path: inset(0 0 0 0);
  }
}
@keyframes clip-text {
  from {
    -webkit-clip-path: inset(0 100% 0 0);
    clip-path: inset(0 100% 0 0);
  }
  to {
    -webkit-clip-path: inset(0 0 0 0);
    clip-path: inset(0 0 0 0);
  }
}
@-webkit-keyframes text-revealer {
  0%, 50% {
    transform-origin: 0 50%;
  }
  60%, 100% {
    transform-origin: 100% 50%;
  }
  60% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
@keyframes text-revealer {
  0%, 50% {
    transform-origin: 0 50%;
  }
  60%, 100% {
    transform-origin: 100% 50%;
  }
  60% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
